@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}

.faq-section {
  margin-top: 100px;

  .faq-section-bg {
    background-color: #f2faf9;
    
    .content {
      margin: auto auto;
      padding: 55px;

      .center-align-row {
        padding-top: 55px;
        margin: auto;
      }

      .col-md-8 {
        margin:auto
      }

      .faq-wrapper {
        float:none;

        @media screen and (min-width: 990px){
          float: right;
        }

        .faq-icon {
          width: 100%;
  
          @media screen and (min-width: 768px){
            /* margin: auto auto;
            position: absolute;
            z-index: 20;
            width: 100%;
            height: 100%; */
          }
  
          img {
            width: 150px;
  
            @media screen and (min-width: 768px){
              width: 250px;
            }
          }
        }
    
        .faq-profile {
  
          @media screen and (min-width: 768px){
            margin: auto auto;
            width: 400px;
            height: 400px;
            object-fit: contain;
            z-index: 10;
            margin-left:100px;
          }
  
          img {
            height: 250px;
            width: 250px;
  
            @media screen and (min-width: 768px){
              position: absolute;
              height: 400px;
              width: 400px;
              object-fit: contain;
            }
          }
        }
      }
      .faq-desc-wrapper {
        width: 100%;
  
        .title {
          font-size: $font-size-big;
          font-weight: $font-weight-heavy;
          color: $color-title;
          text-align: left;
  
          .desc {
            font-size: $font-size-small;
            color: $color-body;
            font-weight: $font-weight-normal;
            width: 100%;
            
            @media screen and (min-width: 1440px) {
              width: 60%;
            }
          }
  
          .faq-btn {
            width: 150px;
            height: 72px;
            border-radius: 36px;
            margin-top: 22px;

            @media screen and (min-width: 768px) {
              width: 200px;
              height: 72px;
              border-radius: 36px;
              margin-top: 22px;
            }
          }
        }
      }
    }
  }
}