@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}

$horizontal-anim-duration: 90s;

.categories {
  overflow: hidden;

  .background-overlay {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: -10;
    width: 100vw;
    margin-top: -30px;

    @media screen and(min-width: 820px){
      width: 800px;
    }
  }
  
  .content {
    padding-bottom: 20px;

    .content-row {
      text-align: center;
      justify-content: center;
      margin-bottom: 24px;
    }

    .title {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;

      @media screen and (min-width: 1440px) {
        font-size: $font-size-extra-big;
      }
    }

    .title-sec {

      @extend %standard-font;
      font-family: $font-after-night;
      font-size: $font-size-big;
      color: $color-hover;
      padding: 0 10px;

      @media screen and(min-width: 768px){
        font-size: $font-size-extra-big;
      }
    }

    .cat-container {
      position: relative;
      width: 100%;
      justify-content: center;
      margin: auto;
      max-width: 1000px;

      .cat-link {
        margin: 4%;

        @media screen and(min-width: 1440px){
          margin: 3%;
        }

        .cat-btn {
          width: 120px;
          height: 120px;
          border-radius: 28px;
          background-image: linear-gradient(to bottom, #eff8ff, #eff8ff);
          box-shadow: 0 35px 30px 0 rgba(70, 115, 122, 0.2);
          text-align: center;

          &:before {
            border-radius: 58px;
            background-image: linear-gradient(to bottom, #eff8ff, #eff8ff);
          }
  
          @media screen and (min-width: 500px) {
            width: 200px;
            height: 200px;
            border-radius: 58px;
            background-image: linear-gradient(to bottom, #eff8ff, #eff8ff);
            box-shadow: 0 35px 30px 0 rgba(70, 115, 122, 0.2);
            text-align: center;

            &:before {
              border-radius: 58px;
              background-image: linear-gradient(to bottom, #eff8ff, #eff8ff);
            }
          }
  
          @media screen and (min-width: 1540px) {
            transition: all 0.4s ease-in-out;
            margin: 2% 7% !important;
            
            &:before {
              border-radius: 58px;
              background-image: linear-gradient(to bottom, #eff8ff, #eff8ff);
            }
          }
  
          .cat-icon{
            height: 40px;
            width: 40px;

            @media screen and (min-width: 500px) {
              width: 60px;
              height: 60px;
            }
          }
          
          p {
            font-family: $font-open-sans;
            font-size: $font-size-smallest;
            font-weight: $font-weight-bold;
            padding-top: 10px;
            color: $color-body;

            @media screen and (min-width: 500px) {
              font-family: $font-open-sans;
              font-size: $font-size-regular;
              font-weight: $font-weight-bold;
              padding-top: 20px;
              color: $color-body;
            }
          }
        }
      } 
      // individual style buttons
      .cat-link:nth-child(2) {
       
        .cat-btn {
          background-image: linear-gradient(to bottom, #f3f0ff, #f3f0ff) !important;
        
          &:before {
            background-image: linear-gradient(to bottom, #f3f0ff, #f3f0ff) !important;
          }
        }
      }
      .cat-link:nth-child(3) {
        
        .cat-btn {
          background-image: linear-gradient(to bottom, #fff6f9, #fff6f9) !important;
        
          &:before {
            background-image: linear-gradient(to bottom, #fff6f9, #fff6f9) !important;
          }
        }
      }
      .cat-link:nth-child(4) {
        
        .cat-btn {
          background-image: linear-gradient(to bottom, #fff9f1, #fff9f1) !important;
        
          &:before {
            background-image: linear-gradient(to bottom, #fff9f1, #fff9f1) !important;
          }

          p {
            font-size: $font-size-tiny;

            @media screen and (min-width: 500px){
              font-size: $font-size-small;
            }
          }
        }
      }
      .cat-link:nth-child(5) {

        .cat-btn {
          background-image: linear-gradient(to bottom, #eefdfd, #eefdfd) !important;
        
          &:before {
            background-image: linear-gradient(to bottom, #eefdfd, #eefdfd) !important;
          }
        }
      }
      .cat-link:nth-child(6) {
        
        .cat-btn {
          background-image: linear-gradient(to bottom, #fdf7ff, #fdf7ff) !important;
        
          &:before {
            background-image: linear-gradient(to bottom, #fdf7ff, #fdf7ff) !important;
          }
        }
      }
      .cat-link:nth-child(7) {
        
        .cat-btn {
          background-image: linear-gradient(to bottom, #fffcec, #fffcec) !important;
        
          &:before {
            background-image: linear-gradient(to bottom, #fffcec, #fffcec) !important;
          }
        }
      }
    }
  }
}